import React, { Component } from "react";
import Workers from "./components/Employees";
import employees from "./employee.json";
import Search from "./components/Search";
import Sort from "./components/Sort";
import NavBar from "./components/NavBar";


class App extends Component {
  // defining the state
  state = {
    employees: employees,
    searchedEmployee: "a"
  };

  // input change handled here
  handleInputChange = event => {
    event.preventDefault()
    const { value } = event.target;
    console.log(value);

    this.setState({
      searchedEmployee: value
    })

    console.log(this.state.searchedEmployee);

  }

  handleSubmit = event => {
    event.preventDefault();
    let filteredEmployee = this.state.employees.filter((employee) => {
      return employee.name === this.state.searchedEmployee
    })
    console.log(filteredEmployee);
    this.setState({
      employees: filteredEmployee
    })
  }

  // function to sort by id
  sortById = () => {
   let sortedArr = this.state.employees.sort(function (a, b) {
      return a.id - b.id
    })
    console.log(sortedArr);
    this.setState({
      employees: sortedArr
    })
  }


  // Map over this.state.friends and render a FriendCard component for each friend object
  render() {
    return (
      <>
      <NavBar />
        <Search
          searchWithFname={this.handleSubmit}
          handleInputChange={this.handleInputChange}
        />
        <Sort sortById={this.sortById} />

        {this.state.employees.map(employee => (
          <Workers
            key={employee.id}
            id={employee.id}
            name={employee.name}
            image={employee.image}
            occupation={employee.occupation}
            location={employee.location}
          />
        ))}
      </>
    );
  }
}

export default App;

