import React from "react";




function Search(props) {
    return (
        <form>
            <form className="form">
                <input
                    value={props.value}
                    name="firstName"
                    onChange={props.handleInputChange}
                    type="text"
                    placeholder="First Name"
                />
                <button onClick={props.searchWithFname} >Submit</button>
            </form>
        </form>)
}



export default Search;